@import url(https://fonts.googleapis.com/css?family=Roboto:100);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AllRoutes_loading__30QBI {
  display: inline-block;
  width: 55px;
  height: 55px;
  margin: 50px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid rgba(255,255,255,.3);
  border-radius: 50%;
  border-top-color: rgb(0, 0, 0);
  animation: AllRoutes_spin__2qp6i 1s ease-in-out infinite;
  -webkit-animation: AllRoutes_spin__2qp6i 1s ease-in-out infinite;
}

@keyframes AllRoutes_spin__2qp6i {
  to { -webkit-transform: rotate(360deg); }
}
@-webkit-keyframes AllRoutes_spin__2qp6i {
  to { -webkit-transform: rotate(360deg); }
}
